this.Prompt_Group_Reservation_cls = <webix.ui.window> webix.ui({
  view: "window",
  type: "icon",
   icon: "wxi-cog",
  close: true, 
  modal: true,
  id: "Prompt_Group_Reservation",
  head:webix.i18n.text.labels["GroupReservation"],
  position: "center",
  width: 400,
  height: 140,
  body: {
      view: "form",  
      width: 400,   
      height: 140, 
      elements: [       
        {
          cols:[
            {         
              rows:[
                {
                  id: "AddOn",
                  name:"AddOn",
                  view: "switch",                 
                  label:webix.i18n.text.labels["AddOn"] ,    
                  value: 1 ,
                  width:150,
                  on: {
                    onChange: function (value) {
                      
                      var GropRes = sessionStorage.getItem("GropRes");
                      if(GropRes != "1")
                      {
                      if(value == "0")
                      {
                        $$("NoofAddOn").hide();  
                        $$("NoofSplit").show();     
                        $$("Split").setValue("1");     
                        $$("AddOn").setValue("0");                          
                      }
                      else
                      {

                        $$("NoofSplit").hide();   
                        $$("NoofAddOn").show();    
                        $$("AddOn").setValue("1");       
                        $$("Split").setValue("0");                                           
                      }
                    }  
                    else{
                      if(value == "0")
                      {
                        $$("AddOn").setValue("1");       
                      }
                    }                 
                    }
                 }
                 },   
                 {
                  id: "Split",
                  name:"Split",
                  view: "switch",                 
                  label:webix.i18n.text.labels["Split"] ,    
                  value: 0  ,
                  on: {
                    onChange: function (value) {                      
                      if(value == "0")
                      {
                        $$("NoofSplit").hide();   
                        $$("NoofAddOn").show();    
                        $$("AddOn").setValue("1");       
                        $$("Split").setValue("0");                            
                      }
                      else
                      {
                        $$("NoofAddOn").hide();  
                        $$("NoofSplit").show();     
                        $$("Split").setValue("1");     
                        $$("AddOn").setValue("0");                            
                      }
                    }
                 }
                 },                    
                ]
              },  
              {
             
              rows:[
                {  
                  id: "NoofAddOn",
                  name:"NoofAddOn",
                  view: "text",                 
                  label:webix.i18n.text.labels["NoofAddOn"] ,   
                  labelWidth:100,
                  maxWidth:170,                                
                 },     
                 {  
                  id: "NoofSplit",
                  name:"NoofSplit",
                  view: "text",                 
                  label:webix.i18n.text.labels["NoofSplit"] ,   
                  labelWidth:100,
                  maxWidth:170,
                  hidden:true,                
                 },                  
                ]
              } , 
          ]
        }  ,
        {
             
          cols:[
            {  
             
             },     
             {  
             cols:[
               {
                 view:'button',
                 label:'Create',
                 width:60,
                 on: {
                  onItemClick: function () {
                    
                   var noofsplitval = $$("NoofSplit").getValue();   
                   var noofAddonval = $$("NoofAddOn").getValue();    
                   var AddonChk = $$("AddOn").getValue();       
                   var splitChk = $$("Split").getValue();   
                   if(AddonChk == "1")
                   { 
                   if(noofAddonval == "" || noofAddonval == "0" || noofAddonval == undefined || noofAddonval == null)
                   {
                    ResComponent.validationMessage("No of Add On cannot be Empty.");
                    return false;
                   }
                   }
                   if(splitChk == "1")
                   { 
                   if(noofsplitval == "" || noofsplitval == "0" || noofsplitval == undefined || noofsplitval == null)
                   {
                    ResComponent.validationMessage("No of Split cannot be Empty.");
                    return false;
                   }
                   }
                  ResComponent.GroupReservationCreation(noofsplitval,noofAddonval,AddonChk,splitChk);  
                  }
               }
               },
               {
                view:'button',
                label:'Cancel',
                width:60,
                on: {
                  onItemClick: function () {
                    $$("Prompt_Group_Reservation").hide();
                  }
               }
              }
             ]
             },                  
            ]
          } 

      ]
  }
});

