  public TariffLoad() {
     
    var GrdId= sessionStorage.getItem("rowindex");      
    var getval =  $$("Grid_Reservation").getItem(GrdId);   
              var RoomTYpe = getval.RES_TYNM;
               var ARRV = this.datepipe.transform(getval.ARRV,'dd/MM/yyyy');
              var DEPART = this.datepipe.transform(getval.DEPART,'dd/MM/yyyy');
              var ADLT = getval.ADLT;
              var CHLD = getval.CHLD;
              var CHLD1 = getval.CHLD1;
              var CHLD2 = getval.CHLD2;
              var R_TY = getval.R_TY_ID;
              var TARF = getval.TARF;
              var PLAN = getval.PLAN;
              var PAX = Number(ADLT) + Number(CHLD) + Number(CHLD1) + Number(CHLD2);           
              var ddlcurrency = this.pgindic.BASE_CURRENCY_ID; 
              this.dataService.TariffLoad_Values(ddlcurrency,RoomTYpe,R_TY,ARRV,DEPART,ADLT,CHLD,CHLD1,CHLD2,PAX,PLAN)
              .subscribe((data: any)=>{   
                          
              if (data != "Tariff is not available for the selected Date Range" && data != "[]" && data != null) {
                var Grid_Data = JSON.parse(data);
                var TOT_TRF = Grid_Data[0].TOT_TRF;
                var T_CNT = Grid_Data[0].T_CNT;
               // $("#txtTariffChargePax").val(T_CNT);
               // $("#txtTotalTariff").val(Number(TOT_TRF).toFixed(2));
                $$("txt_Net_Traffic").setValue(Number(TOT_TRF).toFixed(2));
              //  $("#txtnettrfamnt").val(Number(TOT_TRF).toFixed(2));
              //  $("#txtTarif").val(Number(TOT_TRF));               
                getval.TARF = Number(TOT_TRF).toFixed(2);
                getval.T_CNT = T_CNT;
                $$("Grid_Reservation").refresh();
            }
              });
              };

  public DateCalulate(id){  
    
    var GrdId= sessionStorage.getItem("rowindex");                    
    var getval = $$("Grid_Reservation").getItem(GrdId);    
   var Arrival_Dt:any;
   var Departre_Dt:any;
   var nights:any;   
    Arrival_Dt = getval.ARRV;
    Departre_Dt = getval.DEPART;
    nights = getval.NIGHTS;  
    if( getval.NIGHTS != "")
    {
      var FirstDt:any;
      var lastDt:any;
      const oneDay = 24 * 60 * 60 * 1000;
      FirstDt = new Date(Arrival_Dt);
      lastDt = new Date(Departre_Dt);

    if(id == 1)
    {       
        var diffDays = Math.abs((FirstDt - lastDt)/oneDay); 
        getval["NIGHTS"] = diffDays;  
        $$("Grid_Reservation").refresh();
        $$("Grid_Reservation").updateItem(GrdId,getval);
    }
    else{     
      FirstDt.setDate(FirstDt.getDate()+Number(nights));   
       getval["DEPART"] = FirstDt;   
       $$("Grid_Reservation").refresh();      
       $$("Grid_Reservation").updateItem(GrdId,getval);
    }
  }
  }

