public DataBoundFn(GID)
{
   this.dataService.GetResAdvNetAmnt_Values(GID)
  .subscribe((data:any)=>{ 
    var ResVal = JSON.parse(data);
    $$("advnettff").setValue(ResVal[0].NETTRIFF);
    $$("advresaviladv").setValue(ResVal[0].DEPOSITE);
    $$("txtCurrency").setValue(ResVal[0].CURRUNCY);
    
  });
} 


public DataVoucharNo(VOUCHARNO)
{
   this.dataService.GetResVouchar_Values(VOUCHARNO)
  .subscribe((data:any)=>{ 
    var ResVal = JSON.parse(data);   
    $$("advrestrstyp").setValue(ResVal[0].B_ID);
    $$("PayMentMode").setValue(ResVal[0].SETL_MODE_ID);
    $$("advresamt").setValue(ResVal[0].AMT);
    $$("advsaccode").setValue(ResVal[0].HCD);
    $$("taxClass").setValue(ResVal[0].TAX_C);
    $$("Advresnarr").setValue(ResVal[0].NARRATION);
    $$("ddlcompany").setValue(ResVal[0].CC_PARTY_ID);   
    $$("creditcrd").setValue(ResVal[0].CC_NO);
    $$("cc_auth").setValue(ResVal[0].BANK_NM);
    $$("cc_exp_mth").setValue(ResVal[0].Month);
    $$("cc_exp_yr").setValue(ResVal[0].Year);
  
    if($$("PayMentMode").getValue() == "2")
    {
    $$("RForm7").show();
    $$("RForm8").hide();              
    $$("RForm9").hide();
    }
    else if($$("PayMentMode").getValue() == "5")
    {
    $$("RForm7").hide();
    $$("RForm8").show();              
    $$("RForm9").hide();
    }
    else if($$("PayMentMode").getValue() == "8")
    {
    $$("RForm7").hide();
    $$("RForm8").hide();              
    $$("RForm9").show();
    }
    else
    {
      $$("RForm7").hide();
      $$("RForm8").hide();              
      $$("RForm9").hide();
    }
    
  });
} 

