/**
 * New Open Modes
 */
public EnableModes(Mode) {

  if(Mode == "N")
  {
    this.FnEnable();
    $$("BtnSave").enable();
    $$("BtnDelete").hide();
    $$("BtnNew").disable();
    $$("BtnOpen").disable();
    $$("BtnView").disable();
  }
  else  if(Mode == "O")
  {
    this.FnEnable();
    $$("BtnSave").enable();
    $$("BtnDelete").hide();
    $$("BtnNew").disable();
    $$("BtnOpen").disable();
    $$("BtnView").disable();
  }
  else  if(Mode == "V")
  {
    this.FnDisable();
    $$("BtnSave").enable();
    $$("BtnDelete").show();
    $$("BtnNew").disable();
    $$("BtnOpen").disable();
    $$("BtnView").disable();
    $$("advvochr").enable();
  }
  else  if(Mode == "R")
  {
    this.FnDisable();
    $$("BtnSave").enable();
    $$("BtnDelete").show();
    $$("BtnNew").enable();
    $$("BtnOpen").enable();
    $$("BtnView").enable();
 
  }


}

