public Btn_Reservation_Save_Click(){
  debugger;
  if($$("advResNo").getValue() == "")
  {
  this.ValidationResError("Reservation No Cannot be empty");
  return false;
  }
  var Rddl_Property = this.Rddl_Property_cls.getValue();
  var RForm2Val = JSON.stringify($$("RForm2").getValues(),null,2);
  var RForm3Val = JSON.stringify($$("RForm3").getValues(),null,2);
  var RForm4Val = JSON.stringify($$("RForm4").getValues(),null,2);
  var RForm5Val = JSON.stringify($$("RForm5").getValues(),null,2);
  var RForm6Val = JSON.stringify($$("RForm6").getValues(),null,2);
  var RForm7Val = JSON.stringify($$("RForm7").getValues(),null,2);
  var RForm8Val = JSON.stringify($$("RForm8").getValues(),null,2);
  var RForm9Val = JSON.stringify($$("RForm9").getValues(),null,2);

  var dataparam = {};
  dataparam["MODE"] = localStorage.getItem("MODE");
  dataparam["E_ID"] = localStorage.getItem("EID");
  dataparam["C_Vouch_Gen_Ind"] = localStorage.getItem("C_Vouch_Gen_Ind");
  dataparam["W3_Ind"] = localStorage.getItem("W3_Ind");
  dataparam["I14_Ind"] = localStorage.getItem("I14_Ind");
  dataparam["NN_IND"] = localStorage.getItem("NN_IND");
  dataparam["EXT_CHRG_EDIT_IND"] = localStorage.getItem("EXT_CHRG_EDIT_IND");
  
    dataparam["Rddl_Property"] = Rddl_Property;    
    dataparam["RForm2Val"] = RForm2Val;
    dataparam["RForm3Val"] = RForm3Val;
    dataparam["RForm4Val"] = RForm4Val;
    dataparam["RForm5Val"] = RForm5Val;
    dataparam["RForm6Val"] = RForm6Val;
    dataparam["RForm7Val"] = RForm7Val;
    dataparam["RForm8Val"] = RForm8Val;
    dataparam["RForm9Val"] = RForm9Val;
    var DataVal = JSON.stringify(dataparam);

  this.dataService.AdvanceReservationSave(DataVal)
  .subscribe((data: any)=>{
  var ResponceVal = JSON.parse(data);
  if(ResponceVal != "0")
  {
    this.ValidationResError("Reservation No ="+ResponceVal+" Saved successfully");
  this.Clear();
    return false;
  }
  });

  }


