    Flot_Package() {
        debugger;
        var GrdId = sessionStorage.getItem("rowindex");
        var getval = $$("Grid_Reservation").getItem(GrdId);
        if (getval != undefined) {
            if (getval.RES_TYNM != "") {
                var PK_ID = localStorage.getItem("PK_ID");
                if (PK_ID == "" || PK_ID == null || PK_ID == undefined) {
                    var RES_TYPE = getval.RES_TYNM;
                    var DEPART = this.datepipe.transform(getval.DEPART, 'dd/MM/yyyy');
                    var CURRENCY = this.pgindic.BASE_CURRENCY_ID;
                    var RATE_TY_ID = getval.R_TY_ID;
                    var PARTY_ID = "";
                    var RATE_LOADID = "";
                    this.dataService.Rate_Code_Values(RES_TYPE, DEPART, CURRENCY, RATE_TY_ID, PARTY_ID, RATE_LOADID)
                        .subscribe((data) => {
                        var RateCodeval = JSON.parse(data);
                        if (RateCodeval.RATE_TARIFF.length > 0) {
                            localStorage.setItem("PK_ID", RateCodeval.RATE_TARIFF[0].PK_ID);
                            PK_ID = RateCodeval.RATE_TARIFF[0].PK_ID;
                        }
                    });
                }
                this.dataService.Package_Search_Load(PK_ID).subscribe((data) => {
                    var packagesrch = JSON.parse(data);
                    if (packagesrch != "") {
                        $$("Package_Search_Grid").clearAll();
                        $$("Package_Search_Grid").parse(packagesrch);
                        $$("Package_Search_Grid").refresh();
                    }
                    $$("Prompt_Package_Search").show();
                });
            }
            else
                this.validationMessage("Room Type Cannot be empty.");
        }
        else
            this.validationMessage("Room Type Cannot be empty.");
    }
