    Reservation_Validation() {
        var ReserveGrid = $$("Grid_Reservation").serialize();
        var LastRowData = ReserveGrid[0];
        if (LastRowData.RES_TYNM == "") {
            this.validationMessage("Room Type Cannot be Empty.");
            return false;
        }
        if (LastRowData.ARRV == "") {
            this.validationMessage("Arraival Date Cannot be Empty.");
            return false;
        }
        if (LastRowData.DEPART == "") {
            this.validationMessage("Departure Date Cannot be Empty.");
            return false;
        }
        if (LastRowData.ARRV_TM == "") {
            this.validationMessage("Arraival Time Cannot be Empty.");
            return false;
        }
        if (LastRowData.DEPART_TM == "") {
            this.validationMessage("Departure Time Cannot be Empty.");
            return false;
        }
        if (LastRowData.ADLT == "" || LastRowData.ADLT == "0" || LastRowData.ADLT == undefined) {
            this.validationMessage("Pax Cannot be Empty.");
            return false;
        }
        if (LastRowData.R_TY == "") {
            this.validationMessage("Rate Type Cannot be Empty.");
            return false;
        }
        //if (pgindic.PLAN_APPL_IND == 1) {
        //    if (LastRowData.PLAN == "") {
        //       this.validationMessage("Paln Cannot be Empty.");                  //      
        //        return false;
        //    }
        //}
        if (LastRowData.RES_STS == "") {
            this.validationMessage("Status  Cannot be Empty.");
            return false;
        }
        // if (pgindic.B7_IND == 1) {
        var Pax = Number(LastRowData.ADLT) + Number(LastRowData.CHLD) + Number(LastRowData.CHLD1) + Number(LastRowData.CHLD2);
        var GrdGuest = $$("Grid_Guest").serialize();
        if (GrdGuest.length != 0) {
            if (GrdGuest.length > Pax) {
                this.validationMessage("No of Pax Should be Equal to " + Pax + "(Created Guests).");
                return false;
            }
        }
        if (GrdGuest.length != 0) {
            var idx = Number(GrdGuest.length - 1);
            if (idx < 0)
                idx = idx * -1;
            if ((GrdGuest[idx].GUST_LNM == "" && GrdGuest[idx].GUST_FNM == "") || (GrdGuest[idx].GUST_LNM == undefined && GrdGuest[idx].GUST_FNM == undefined)) {
                this.validationMessage("Guest Name Cannot be Empty.");
                return false;
            }
        }
        // }      
        return true;
    }
